.colorbtn{
  position:absolute;
  width: 200px;
  height: 200px;
  border: 1px solid rgb(226, 165, 33) ;
}
.container{
  margin: 0;
  padding: 0;
  height: 25vh;
  text-align: center;
  border: 5px solid black;
  background-color: rgb(205, 247, 20);
  background: radial-gradient(rgb(203, 203, 230),rgb(190, 173, 16),red);
}
.container:hover{
  transition: 3s;
  cursor: pointer;
}